'use strict';

import CreateStyleSheet from './CreateStyleSheet';
import RandomMessage from './RandomMessage';
import CreateElement from './CreateElement';
import ShowMessage from './ShowMessage';
import EventOpenCloseChat from './EventOpenCloseChat';


export default class Widget {

    init(){

        // –ö–ª–∞—Å—Å —Å–æ–∑–¥–∞–Ω–∏—è –∏ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è <style>
        let createStyleSheet  = new CreateStyleSheet();

        // –ö–ª–∞—Å—Å –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ —á–∏—Å–µ–ª 
        let randomMessage = new RandomMessage();

        // –ö–ª–∞—Å—Å —Å–æ–∑–¥–∞–Ω–∏—è html —ç–ª–µ–º–µ–Ω—Ç–æ–≤
        let —ÅreateElement = new CreateElement('–ê—Ä—Ç–µ–º –ß–∏—á–µ—Ä–∏–Ω', 'DB Engineer', 'assets/img/news-01.jpg');

        // –ö–ª–∞—Å—Å –ø–æ–∫–∞–∑–∞ —Å–æ–æ–±—â–µ–Ω–∏–π –≤ —á–∞—Ç–µ
        let showMessage = new ShowMessage();
        
        //
        let eventOpenCloseChat = new EventOpenCloseChat();

        function firstMsg(message){

            setTimeout(function(){
            
                let divMsg = —ÅreateElement.createMessage(false, message);
                // –ü–æ–∫–∞–∑—ã–≤–∞—é —Å–æ–æ–±—â–µ–Ω–∏–µ
                showMessage.show(divMsg);
                // –î–æ–±–∞–≤–ª—è—é –≤ –º–∞—Å—Å–∏–≤ —Å–æ–æ–±—â–µ–Ω–∏–µ
                messageArray.push({name: 'manager', msg: message})
            }, 700);
        }

        function replyAi(){
            setTimeout(function(){
                // –≥–µ–Ω–µ—Ä–∏—Ä—É—é —Å–ª—É—á–∞–π–Ω–æ–µ —á–∏—Å–ª–æ, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –≤—ã–±–∏—Ä–∞—é —Å–æ–æ–±—â–µ–Ω–∏–µ –∏–∑ –º–∞—Å—Å–∏–≤–∞
                let msgText = arrayMesgAi[ randomMessage.random(arrayMesgAi.length-1) ]; 
             
                // —Å–æ–∑–¥–∞—é HTML —Å–æ–æ–±—â–µ–Ω–∏—è
                let msg = —ÅreateElement.createMessage(false, msgText);

                // –ø–æ–∫–∞–∑—ã–≤–∞—é 
                showMessage.show(msg);

                // –î–æ–±–∞–≤–ª—è—é –≤ –º–∞—Å—Å–∏–≤ —Å–æ–æ–±—â–µ–Ω–∏–µ
                messageArray.push({name: 'manager', msg: msgText})

                // –ê–≤—Ç–æ–ø—Ä–æ–∫—Ä—É—Ç–∫–∞ —Å–∫—Ä–æ–ª–ª–∞
                let scrollBodyMessage = document.getElementById('spchat__body');
                scrollBodyMessage.scrollTop = scrollBodyMessage.scrollHeight;

            }, 1000)

        }
        /**************START******************/

        // –§–ª–∞–≥ online
        let onlineUser = false;

        // –§–ª–∞–≥, —á–∞—Ç –æ—Ç–∫—Ä—ã—Ç/–∑–∞–∫—Ä—ã—Ç
        let chatOpen = false;

        // –í—ã–±–æ—Ä –∫–Ω–æ–ø–∫–∏ –∑–∞–∫—Ä—ã—Ç—å –¥–ª—è –∑–∞–∫—Ä—ã—Ç–∏—è —á–∞—Ç–∞ 
        let chatIdClose = document.getElementById('spchat__close');

        // –í—ã–±–æ—Ä header —á–∞—Ç–∞
        let chatIdHead = document.getElementById('spchat__header');
        
        // –§–ª–∞–≥, –æ—Ç–≤–µ—Ç–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
        let userReply = false;

        //
        let messageArray = [];

        // –í—Ä–µ–º–µ–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ –æ—Ç–≤–µ—Ç–æ–≤ –º–µ–Ω–µ–¥–∂–µ—Ä–∞
        let arrayMesgAi = [
            ' –ö–∞–∫ –∫ –í–∞–º –æ–±—Ä–∞—â–∞—Ç—å—Å—è ',
            ' –ú–Ω–µ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –≤—Ä–µ–º—è –Ω–∞ —É—Ç–æ—á–Ω–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ ',
            ' –ü–æ–∫–∞ –∂–¥—É –æ—Ç–≤–µ—Ç–∞ –æ—Ç —Ä—É–∫–æ–≤–æ–¥—Å—Ç–≤–∞, –º–æ–≥—É —Ä–∞—Å—Å–∫–∞–∑–∞—Ç—å –∞–Ω–µ–∫–¥–æ—Ç ',
            ' –û–¥–Ω—É –º–∏–Ω—É—Ç—É, –ø–æ–∂–∞–π–ª—É—Å—Ç–∞ ',
            ' –í—Ä–µ–º—è –æ–∂–∏–¥–∞–Ω–∏—è –æ—Ç–≤–µ—Ç–∞ –∏—Å—Ç–µ–∫–ª–æ. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –æ–±—Ä–∞—Ç–∏—Ç—å—Å—è –ø–æ–∑–∂–µ ',
            ' –°–æ–æ–±—â–∏—Ç–µ —Å–≤–æ–π e-mail, —è –Ω–∞ –Ω–µ–≥–æ –ø–æ–∑–∂–µ –æ—Ç–≤–µ—á—É',
            ' –ö–∞–∫ —Å –í–∞–º —Å–≤—è–∑–∞—Ç—å—Å—è?',
            ' –ü—Ä–æ—à—É –í–∞—Å –ø–æ–¥–æ–∂–¥–∞—Ç—å, —è –æ—Ç–≤–µ—á—É —á–µ—Ä–µ–∑ –Ω–µ—Å–∫–æ–ª—å–∫–æ –º–∏–Ω—É—Ç '
        ];

        // –°–æ–±—ã—Ç–∏–µ –Ω–∞ –∑–∞–≥—Ä—É–∑–∫—É –¥–æ–∫—É–º–µ–Ω—Ç–∞
        document.addEventListener('DOMContentLoaded', listenerDOMContentLoaded);

        function listenerDOMContentLoaded(){

            // —á–µ—Ä–µ–∑ 3,5—Å–µ–∫ –≤—ã–ø–æ–ª–Ω–∏—Ç—å 
            setTimeout(function(){
                // –ï—Å–ª–∏ —á–∞—Ç –∑–∞–∫—Ä—ã—Ç—å, —Ç–æ –æ—Ç–∫—Ä—ã—Ç—å
                if(!chatOpen) {

                    // chatOpen = eventOpenCloseChat.openClose(chatOpen);
                    listenerOpenClose()

                    console.log('3.5');
                } 

            }, 3500);
             
        }

        // // –°–æ–±—ã—Ç–∏–µ –Ω–∞ –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç—å –¥–æ–∫—É–º–µ–Ω—Ç–∞
        // window.addEventListener("focus", function() {

        //     if (onlineUser) {
        //         // –û—Ç–∫—Ä—ã—Ç—å —á–∞—Ç –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –ø–æ –∏—Å—Ç–µ—á–µ–Ω–∏—é 3,5—Å
        //         setTimeout(function(){
        //             eventOpenChat();
        //             console.log('setTimeout - ok') 
        //         }, 4500);
        //     }
            
        //     console.log('active') 
        // });

        // // –°–æ–±—ã—Ç–∏–µ –Ω–∞ —É—Ö–æ–¥ —Å–æ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
        // window.addEventListener("blur", function() {
        //     onlineUser = true;
        // });


        // –í–µ—à–∞—é —Å–æ–±—ã—Ç–∏–µ –∫–ª–∏–∫ –Ω–∞ header 
        chatIdHead.addEventListener('click', listenerOpenClose);

        function listenerOpenClose(){

            chatOpen = eventOpenCloseChat.openClose(chatOpen);

            if (chatOpen) {
                
                // —É–¥–∞–ª—è—é –ø—Ä–æ—Å–ª—É—à–∫—É —Å–æ–±—ã—Ç–∏—è –ø–æ header —á–∞—Ç–∞
                chatIdHead.removeEventListener('click', listenerOpenClose);

                // –í–µ—à–∞—é –ø—Ä–æ—Å–ª—É—à–∫—É —Å–æ–±—ã—Ç–∏—è –Ω–∞ –∫–ø–æ–Ω–∫—É –∑–∞–∫—Ä—ã—Ç–∏–µ
                chatIdClose.addEventListener('click', listenerOpenClose);

                // –ü—É—Å—Ç–æ–π –ª–∏ –º–∞—Å—Å–∏–≤ —Å–æ–æ–±—â–µ–Ω–∏–π
                if (messageArray.length === 0) {
                    // –§—É–Ω–∫—Ü–∏—è 
                    firstMsg('–ó–¥—Ä–∞–≤—Å—Ç–≤—É–π—Ç–µ! –ß–µ–º —è –º–æ–≥—É –≤–∞–º –ø–æ–º–æ—á—å?');
                }
                console.log('—É–¥–∞–ª–∏—Ç—å —Å–æ–±—ã—Ç–∏–µ –Ω–∞ header –∏ –¥–æ–±–∞–≤–ª–µ–Ω–æ –Ω–∞ close');
            } else {
                // –í–µ—à–∞—é —Å–æ–±—ã—Ç–∏–µ –∫–ª–∏–∫ –Ω–∞ header 
                chatIdHead.addEventListener('click', listenerOpenClose);
                // —É–¥–∞–ª—è—é –ø—Ä–æ—Å–ª—É—à–∫—É —Å–æ–±—ã—Ç–∏—è –Ω–∞ –∫–ø–æ–Ω–∫—É –∑–∞–∫—Ä—ã—Ç–∏–µ
                chatIdClose.removeEventListener('click', listenerOpenClose);
                console.log('–¥–æ–±–∞–≤–∏—Ç—å');
            }

        };

        // –ò—â—é –∫–Ω–æ–ø–∫—É textarea
        let sendMessage = document.getElementById('sendMessage');

        // –í–µ—à–∞—é —Å–æ–±—ã—Ç–∏–Ω–∏–µ –Ω–∞ Enter
        sendMessage.addEventListener('keydown', function(e){

            // –ï—Å–ª–∏ –Ω–∞–∂–∞—Ç–∞ –∫–Ω–æ–ø–∫–∞ Enter(13)
            if (e.keyCode === 13) {
                // –û—Ç–º–µ–Ω—è—é –ø–µ—Ä–µ–Ω–æ—Å —Å—Ç—Ä–æ–∫–∏, –¥–µ–π—Å—Ç–≤–∏–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
                e.preventDefault();

                // –ï—Å–ª–∏ —Å–æ–æ–±—â–µ–Ω–∏–µ –Ω–µ –ø—É—Å—Ç–æ–µ
                if( this.value !== '') {

                    // –°–æ–∑–¥–∞—é html —Ä–∞–∑–º–µ—Ç–∫—É —Å —Å–æ–±—â–µ–Ω–∏–µ–º 
                    let msg = —ÅreateElement.createMessage(true, this.value);
                    // 
                    showMessage.show(msg);
                    messageArray.push({name: 'user', msg: this.value})
                
                    // –û—Ç–≤–µ—Ç–∞ –º–∞–Ω–∞–≥–µ—Ä–∞
                    replyAi();
                }

                // –ê–≤—Ç–æ–ø—Ä–æ–∫—Ä—É—Ç–∫–∞ —Å–∫—Ä–æ–ª–ª–∞
                let scrollBodyMessage = document.getElementById('spchat__body');
                scrollBodyMessage.scrollTop = scrollBodyMessage.scrollHeight;

                // –°–±—Ä–∞—Å—ã–≤–∞—é –∑–Ω–∞—á–µ–Ω–∏–µ –ø–æ–ª—è
                sendMessage.value = '';

            }
        });
    }
}