'use strict';

export default class RandomMessage{

    random(lengthArray){
        let rand = 0 - 0.5 + Math.random() * (lengthArray - 0 + 1)
        rand = Math.round(rand);
        // –ï—Å–ª–∏ —Å—Å–≥–µ–Ω–µ—Ä–∏—Ä–æ–≤–∞–Ω–æ -0, —Ç–æ–≥–¥–∞ –ø—Ä–∏—Å–≤–∞–µ–≤–∞–µ–º 0
        if (rand === -0) rand = 0;

        return rand;
    }

}

