'use strict';

export default class EventOpenCloseChat {

    openClose(isOpen){

        // –í—ã–±–æ—Ä —á–∞—Ç–∞ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è/—É–¥–∞–ª–µ–Ω–∏—è –∫–ª–∞—Å—Å–∞ 
        let chatId = document.getElementById('spchat');

        // –ø—Ä–æ–≤–µ—Ä–∫–∞, –æ—Ç–∫—Ä—ã—Ç –ª–∏ —á–∞—Ç
        if (!isOpen) {

            // —É–¥–∞–ª—è—é –∫–ª–∞—Å—Å
            chatId.classList.remove('spchat-hidden');

            return true;

        } else {

            // –¥–æ–±–∞–≤–ª—è—é –∫–ª–∞—Å—Å
            chatId.classList.add('spchat-hidden');

            return false;
        }
    }

}